import React, { Component } from 'react'
import { Tabs } from 'antd';
import moment from 'moment';
import { NavLink } from 'react-router-dom';
import { Fragment } from 'react';
import Slider from "react-slick";

const { TabPane } = Tabs;


export default class HomeMenu_Mobile extends Component {
    rederHeThongRap = () => {
        return this.props.heThongRapChieu?.map((heThongRap,index)=>{
            
            return <TabPane tab={<img src={heThongRap.logo} className="rounded-full" style={{width:'50px'}} />} key={index}>
                 <Tabs defaultActiveKey="1" centered>
                     {heThongRap.lstCumRap?.map((cumRap, index)=>{
                         return <TabPane tab={
                            <div style={{width:'300px', display:'flex'}}>
                              <img src="https://picsum.photos/id/1/200/300" alt="https://picsum.photos/id/1/200/300" width="50"/><br/>
                              <div className="text-left ml-2">
                              {cumRap.tenCumRap.length > 35 ? <span>{cumRap.tenCumRap.slice(0, 35)} ...</span> : <span>{cumRap.tenCumRap} </span>}
                                <p className="text-red-400">Chi tiết </p>
                              </div>
                            </div>
                          } key={index}>
                              {cumRap.danhSachPhim.slice(0,4).map((phim,index) => {
                return <Fragment key={index}>
                  <div className="my-5">
                    <div style={{display: 'flex'}}>
                      <img style={{width:'75px', height:'75px'}}  src={phim.hinhAnh} alt={phim.hinhAnh} onError={(e)=>{e.target.onerror = null; e.target.src="https://picsum.photos/seed/picsum/200/300"}}/>
                      {/* <div style={{ background: `url(${phim.hinhAnh})  , url(https://picsum.photos/1000/500)`, width:'60px',backgroundPosition: 'center', backgroundSize: '100%,100%', backgroundRepeat:'no-repeat', backgroundCover:'center',height:'100px'}}>
                        
                      </div> */}
                      <div className="ml-2">
                        <h1 className="text-2xl text-green-700">{phim.tenPhim} </h1>
                        <p>{cumRap.diaChi} </p>
                        {/* load lịch chiếu phim  */}
                        <div className="grid grid-cols-4 gap-6">
                          {phim.lstLichChieuTheoPhim?.slice(0,10).map((lichChieu, index)=>{
                            return <NavLink className="text-xl text-green-400" to="/" key={index}>
                            {moment (lichChieu.ngayChieuGioChieu).format('hh:mm A')}


                              
                            </NavLink>
                          })}
                        </div>
                      </div>
                    </div>
                  </div>
                  <hr/>
                </Fragment>
              })}
                          </TabPane>
                     })}
                </Tabs>
            </TabPane>
        })
    }
    render() {
        
        return (
            <Tabs defaultActiveKey="1" centered>
                
                {this.rederHeThongRap()}
                
                {/* <TabPane tab="Tab 1" key="1">
                    Content of Tab Pane 1
                </TabPane>
                <TabPane tab="Tab 2" key="2">
                    Content of Tab Pane 2
                </TabPane>
                <TabPane tab="Tab 3" key="3">
                    Content of Tab Pane 3
                </TabPane> */}
            </Tabs>
        )
    }
}
