

import './HomeMenu.module.css'
import { Tabs } from 'antd';
import moment from 'moment';
import { Fragment, PureComponent } from 'react';
import { NavLink } from 'react-router-dom';
const { TabPane } = Tabs;


export default class HomeMenu extends PureComponent {
  state = {
    tabPosition:'left'
  }
  changeTabPosition = e => {
    this.setState({ tabPosition: e.target.value });
  };
  rederHeThongRap = () => {
    return this.props.heThongRapChieu?.map((heThongRap,index)=>{
      let {tabPosition} =this.state;
      return <TabPane tab={<img src={heThongRap.logo} className="rounded-full" style={{width:'50px'}} />} key={index}>
      <Tabs tabPosition={tabPosition}>
        {heThongRap.lstCumRap?.map((cumRap, index) => {
          return <TabPane tab={
            <div style={{width:'300px', display:'flex'}}>
              <img src="https://picsum.photos/id/1/200/300" alt="https://picsum.photos/id/1/200/300" width="50" height="80" /><br/>
              <div className="text-left ml-2 " style={{margin: 'auto' ,display: 'block'}}>
              {cumRap.tenCumRap.length > 35 ? <span>{cumRap.tenCumRap.slice(0, 35)} ...</span> : <span>{cumRap.tenCumRap} </span>}
                <p className="text-red-400">Chi tiết </p>
              </div>
            </div>
          } key={index}>
              {/* Load phim tương ứng */}
              {cumRap.danhSachPhim.slice(0,4).map((phim,index) => {
                return <Fragment key={index}>
                  <div className="my-5">
                    <div style={{display: 'flex'}}>
                      <img style={{width:'75px', height:'125px'}}  src={phim.hinhAnh} alt={phim.hinhAnh} onError={(e)=>{e.target.onerror = null; e.target.src="https://picsum.photos/seed/picsum/200/300"}}/>
                      {/* <div style={{ background: `url(${phim.hinhAnh})  , url(https://picsum.photos/1000/500)`, width:'60px',backgroundPosition: 'center', backgroundSize: '100%,100%', backgroundRepeat:'no-repeat', backgroundCover:'center',height:'100px'}}>
                        
                      </div> */}
                      <div className="ml-2">
                        <h1 className="text-2xl text-blue-500">{phim.tenPhim} </h1>
                        <p className="text-white">{cumRap.diaChi} </p>
                        {/* load lịch chiếu phim  */}
                        <div className="grid grid-cols-6 gap-2">
                          {phim.lstLichChieuTheoPhim?.slice(0,12).map((lichChieu, index)=>{
                            return <NavLink className="text-xl text-center text-white p-2 bg-gray-400 hover:bg-gray-900" to={`/detail/${phim.maPhim}`} key={index}>
                            {moment (lichChieu.ngayChieuGioChieu).format('hh:mm ')}


                              
                            </NavLink>
                          })}
                        </div>
                      </div>
                    </div>
                  </div>
                  <hr/>
                </Fragment>
              })}
          </TabPane>
    })}
      </Tabs>
    </TabPane>  
    })
  }
 
  render() {
    console.log('props123',this.props);
    const {tabPosition} =this.state;
    return (
      <>

        <Tabs tabPosition={tabPosition}>
          {this.rederHeThongRap()}
          
        </Tabs>
      </>
    )
  }
}

